@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box;
}

/* normal */

/* bold */
/* @import "../ReactParallaxTilt.scss"; */
/* ==================================== */

* {
  font-family: "IBM Plex Serif", serif !important;
  letter-spacing: 0.5px;
}
html {
  scroll-behavior: smooth;
}
img {
  object-fit: cover !important;
}

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  background: none !important;
  color: #09c75f !important;
}
.nav-tabs .nav-link {
  border: none !important;
}

.nav-item {
  font-size: 1rem;
}
.MuiButton-root {
  color: white !important;
}
.MuiInput-underline::before {
  border-bottom: 3px solid aliceblue !important;
}
.MuiInput-underline::after {
  border-bottom: 3px solid #09c75f !important;
}
.MuiButton-outlined {
  border: 1px solid rgba(255, 255, 255, 1) !important;
}

.green-text {
  color: #09c75f;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Abril Fatface", cursive !important;
  letter-spacing: 2px;
}

.chonburi-font {
  font-family: "Abril Fatface", cursive !important;
  letter-spacing: 2px;
}

